import React, { useState, useEffect } from "react";
import "./App.css";
import { Navbar, Container, Nav, NavDropdown, Button } from "react-bootstrap";
import Data from "./data.js";
import Detail from "./Detail.js";
import axios from "axios";

import { Link, Route, Switch } from "react-router-dom";

function App() {
  let [shoes, setShoes] = useState(Data);
  let [isLoading, setIsLoading] = useState(false);
  let [stock, setStock] = useState([10, 11, 12]);

  function addShoesItem(props) {
    var _shoes = [...shoes, ...props.data];
    console.log(props.data);
    setShoes(_shoes);
  }

  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
        <Container>
          <Navbar.Brand href="#home">신발가게</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link as={Link} to="/">
                Home
              </Nav.Link>
              <Nav.Link as={Link} to="/detail">
                Detail
              </Nav.Link>
              <NavDropdown title="Dropdown" id="basic-nav-dropdown">
                <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.2">
                  Another action
                </NavDropdown.Item>
                <NavDropdown.Item href="#action/3.3">
                  Something
                </NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item href="#action/3.4">
                  Separated link
                </NavDropdown.Item>
              </NavDropdown>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      <Switch>
        <Route exact path="/">
          <div className="jumboContainer">
            <h1> 20% sale</h1>
            <p>
              안녕하세요. 준따오 쇼핑몰입니다. 오늘부터 20% 세일 들어갑니다.
            </p>
            <p> 많이 이용해 주세요..</p>
            <Button variant="primary">Primary</Button>
          </div>
          <div className="container">
            <div className="row">
              {shoes.map((value, index) => {
                return <ShoesCard data={value} index={index + 1} key={index} />;
              })}
            </div>

            <button
              className="btn btn-primary"
              onClick={() => {
                axios
                  .get("https://codingapple1.github.io/shop/data2.json")
                  .then((result) => {
                    console.log(result);
                    console.log("성공");
                    addShoesItem(result);
                  })
                  .catch(() => {
                    console.log("실패");
                  });
              }}
            >
              더보기
            </button>
          </div>
        </Route>
        <Route path="/detail/:id">
          <Detail shoes={shoes} stock={stock} setStock={setStock}/>
        </Route>
      </Switch>
    </div>
  );
}

function ShoesCard(props) {
  return (
    <div className="col-md-4">
      <img
        src={"https://codingapple1.github.io/shop/shoes" + props.index + ".jpg"}
        width="100%"
        alt=""
      ></img>
      <h4>{props.data.title}</h4>
      <p>{props.data.content}</p>
    </div>
  );
}

export default App;
